@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect {
    background-color: #000091;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect {
    color: #f5f5fe;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect::after {
    background-image: linear-gradient(0deg, #f5f5fe, #f5f5fe), linear-gradient(0deg, #f5f5fe, #f5f5fe);
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect:disabled, a.fr-connect:not([href]) {
    background-color: #e5e5e5;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect:disabled, a.fr-connect:not([href]) {
    color: #929292;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect:disabled::after, a.fr-connect:not([href])::after {
    background-image: linear-gradient(0deg, #929292, #929292), linear-gradient(0deg, #929292, #929292);
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect-group .fr-connect + p a {
    color: #000091;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-connect-group p {
    color: #666;
  }
}
